body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /* overflow: hidden; */
}

.page-container {
  height: 100%;
  position: relative;
  min-height: 100vh;
}

.header {
  background: #366db3;
  padding: 15px;
}

.header .logo-container h2 {
  margin: 0;
  color: #fff;
  font-size: 26px;
  line-height: 32px;
}

.page-content {
  height: 100%;
  padding: 30px 30px 70px;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.d-flex {
  display: -ms-flexbox;
  display: flex;
}

.align-items-center {
  -ms-flex-align: center;
      align-items: center;
}

.justify-content-between {
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.justify-content-center {
  -ms-flex-pack: center;
      justify-content: center;
}

.flex1 {
  -ms-flex: 1 1;
      flex: 1 1;
}

.flex50 {
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-preferred-size: calc(50% - 30px);
      flex-basis: calc(50% - 30px);
  width: calc(50% - 30px);
}

.render-container {
  padding: 10px 15px;
  height: 100%;
  white-space: normal;
  word-break: break-word;
}

.code-visual {
  width: 100%;
  overflow-y: auto;
  margin-top: 50px;
}

.config-container {
  padding: 10px 15px;
  border-left: 1px solid #ddd;
}

.title-container {
  margin-bottom: 20px;
  border-bottom: 1px solid #ddd;
}

.config-container h3 {
  margin-bottom: 5px;
  margin-top: 0;
}

.config-container p {
  margin-bottom: 10px;
  margin-top: 0;
  font-size: 13px;
}

.form-group {
  margin-bottom: 10px;
}

.form-group:last-child {
  margin-bottom: 0;
}

.form-group label {
  display: block;
  font-size: 13px;
  margin-bottom: 5px;
}

.form-group .form-control {
  border-radius: 4px;
  border: 1px solid #ddd;
  font-size: 15px;
  width: 70%;
  max-width: 70%;
  line-height: 20px;
  outline: none;
  padding: 8px;
}

.form-group select.form-control {
  width: 75%;
  max-width: 75%;
}

.footer {
  background: #ccc;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

.footer .copyright {
  margin: 0;
  padding: 15px;
  text-align: center;
  font-size: 14px;
  color: #333;
}



